<template>
  <div class="black-bg" v-if="show">
    <div class="white-bg"> 
        <button @click="closeP" class="close-button">닫기</button>

        <h4> 팝업창1 만들거임</h4>
        <p> 마커1 누르면 뜨는 팝업창 ! </p>

        <div align="center" justipy="center">
            <img src="" id="photoimg" width="100px" height="100px">
            <input type="file" name="photofile" id="photofile" accept="image/*" capture="camera"
            @change="onPhotoFileChange">
            <input type="button" value="!!!카메라!!!"
            @click="triggerCamera" class="cam-button">
        </div>

    </div>
  </div>
</template>

<script>
export default {
    props: {
        show: {
            type: Boolean,
            required: true,
        }
    },

    methods: {
        closeP(){
            this.$emit('close');
        },
        triggerCamera(){
            const photoFileInput = this.$el.querySelector("#photofile");
            photoFileInput.click();
        },
        onPhotoFileChange(event){
            let filename;
            if (window.FileReader) {
                filename = event.target.files[0].name;
            } else {
                filename = event.target.value.split('/').pop().split('\\').pop();
            }
            console.log("파일 사이즈 : " + event.target.files[0].size);
            console.log("파일명 : " + filename);

            this.previewImage(event.target);
        },
        previewImage(inputElement){
            if (inputElement.files && inputElement.files[0]) {
                let reader = new FileReader();
                reader.onload = (e) => {
                    this.$el.querySelector("#photoimg").setAttribute('src', e.target.result);
                    this.$el.querySelector("#photoimg").style.display = "block";
                }
                reader.readAsDataURL(inputElement.files[0]);
            }
        },
    }
}

</script>

<style>
    body{
        margin: 0;
    }
    div{
        box-sizing: border-box;
    }
    .black-bg{
        width: 100%; height: 100%;
        background: rgba(0, 0, 0, 0.5);
        position: fixed; padding: 20px;
        z-index: 1000;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .white-bg{
        width: 80%; height: 80%; 
        background:white;
        border-radius: 8px;
        padding: 50px;
    }
    #photoimg{
        display:None;
    }
    #photofile{
        display:None;
        background: yellow;
    }
    .cam-button{
        z-index: 1000;
        position: relative;
        margin: 0 auto; /* 가운데 정렬 */
        display: block;
        /* bottom: 130px; */
    }
    .close-button{
        position: absolute;
        top: 10px; /* 위로 10px 이동 */
        right: 10px; /* 오른쪽으로 10px 이동 */
    }
</style>